body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.is-invalid {
    border-color: red;
}
span.invalid-feedback {
    font-size: small;
    color: red;
}
input {
    height: 40px;
    display: block;
    width: 100%;
    margin: 10px 0;
    padding-left: 10px;
    box-sizing: border-box;
    padding-right: 10px;
    border-style: solid;
    border-width: 1px;
}

form {
    position: relative;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
}

.text-center {
    text-align: center;
}
.text-center {
    text-align: center;
}

.btn {
    padding: 20px 30px;
    border-radius: 5px;
    margin: 5px;
    display: inline-block;
}

.bg-gray {
    background-color: #ebebeb;
}

.bg-green {
    background-color: #b6f893;
}
textarea {
    height: 200px;
    display: block;
    width: 100%;
    margin: 10px 0;
    padding-left: 10px;
    box-sizing: border-box;
    padding-right: 10px;
    border-style: solid;
    border-width: 1px;
    border-color: currentColor;
    font-family: Roboto, sans-serif;
}
select {
    height: 40px;
    display: block;
    width: 100%;
    margin: 10px 0;
    padding-left: 10px;
    box-sizing: border-box;
    padding-right: 10px;
    border-style: solid;
    border-width: 1px;
    border-color: currentColor;
}
ul.post-list > li.post-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-bottom: 1px dotted #303030;
    padding: 30px 12px;
}

a.button-tag {
    display: block;
    padding: 15px 10px;
    background-color: #7fffd4;
    margin-left: 0px;
    float: right;
    border: none;
    border-radius: 3px;
