    font-size: 14px;
}
time {
    font-family: monospace;
}
h3.title {
    margin-bottom: 10px;
}
body {
    font-family: Roboto, sans-serif;
}
.container {
    display: grid;
    grid-template-columns: 25% auto;
    grid-column-gap: 45px;
    grid-template-areas: "sidebar main";
    max-width: 1200px;
    padding-left: 15px;
    padding-right: 15px;
    margin-top: 50px !important;
    margin: 0 auto;
}
aside {
    grid-area: sidebar;
}
.post-container {
    grid-area: main;
}
* {
    box-sizing: border-box;
}
a {
    text-decoration: none;
    color: currentColor;
}
h1, h2, h3, h4, h5, h6 {
    font-weight: 900;
    margin-top: 0;
}
h1 {
    font-size: 3em;
}
h2 {
    font-size: 2.5em;
}
h3 {
    font-size: 2em;
}
p {
    margin-top: 0;
}

.tags-cloud a {
    padding: 8px;
    background-color: #7fffd4;
    margin-left: 0px;
    display: inline-block;
    margin-right: 5px;
    margin-top: 15px;
    border-radius: 3px;
    font-weight: 700;
    font-size: 14px;
}
