.is-invalid {
    border-color: red;
}
span.invalid-feedback {
    font-size: small;
    color: red;
}
input {
    height: 40px;
    display: block;
    width: 100%;
    margin: 10px 0;
    padding-left: 10px;
    box-sizing: border-box;
    padding-right: 10px;
    border-style: solid;
    border-width: 1px;
}

form {
    position: relative;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
}

.text-center {
    text-align: center;
}